import React from 'react';
import { Link, graphql } from 'gatsby';
import rehypeReact from 'rehype-react';
import { Heading, Paragraph, Badge, Pane, Text } from 'evergreen-ui';
import Layout from '../components/layout';
import SEO from '../components/seo';

function prevNextNavigation(previous, next) {
  return (
    <>
      <Text color="muted">Read my other writings...</Text>
      <nav>
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={`blog${previous.fields.slug}`} rel="prev">
                <Text size={200} color="#429777">← {previous.frontmatter.title}</Text>
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={`blog${next.fields.slug}`} rel="next">
                <Text size={200} color="#429777">{next.frontmatter.title} →</Text>
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </>
  );
}

class BlogPostTemplate extends React.Component {
  render() {
    const post = this.props.data.markdownRemark;
    const siteTitle = this.props.data.site.siteMetadata.title;
    const { previous, next } = this.props.pageContext;

    const renderAst = new rehypeReact({
      createElement: React.createElement,
      components: {
        h1: (props) => (
          <Heading textAlign="center" size={800} {...props}></Heading>
        ),
        h2: (props) => (
          <Heading textAlign="center" size={700} {...props}></Heading>
        ),
        h3: (props) => (
          <Heading textAlign="center" size={600} {...props}></Heading>
        ),
        p: (props) => (
          <Paragraph
            size={500}
            textAlign="justify"
            marginBottom="16px"
            {...props}
          />
        ),
        figcaption: (props) => <Paragraph textAlign="center" {...props} />,
      },
    }).Compiler;

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <Pane marginRight="50px" marginLeft="50px" marginBottom="100px">
          <SEO
            title={post.frontmatter.title}
            description={post.frontmatter.description || post.excerpt}
          />
          <Heading textAlign="center" size={800}>
            {post.frontmatter.title}
          </Heading>
          <Pane display="flex" justifyContent="center">
            {post.frontmatter.tags.map((tag, i) => (
              <Badge key={i} marginRight="10px" color="teal">
                {tag}
              </Badge>
            ))}
          </Pane>
          <Heading marginBottom="16px" textAlign="center" size={200}>
            {post.frontmatter.date}
          </Heading>

          {renderAst(post.htmlAst)}
          <Pane marginTop="32px">{prevNextNavigation(previous, next)}</Pane>
        </Pane>
      </Layout>
    );
  }
}

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      htmlAst
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
      }
    }
  }
`;
